import React, { useState, useLayoutEffect } from 'react';
import CourseCharts from './CourseCharts/CourseCharts';
import PartakerCharts from './PartakerCharts/PartakerCharts';
import CombinedCharts from './CombinedCharts/CombinedCharts';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import TabBigger from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import Spinner from '../../components/UI/Spinner/Spinner';
import axios from 'axios';

// Panel de opciones
const TabPanel = (props) => {
  const { children, value, index, ...other } = props;
  return (
    <div
      role='tabpanel'
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const Charts = () => {
  const [loading, setLoading] = useState(false);
  const [value, setValue] = React.useState(0);
  const [courseData, setCourseData] = useState([]);
  const [partakerData, setPartakerData] = useState([]);

  useLayoutEffect(() => {
    request();
  }, []);

  const request = () => {
    const firstUrl = '/charts-courses/';
    const secondUrl = '/charts-partakers/';

    const requestOne = axios.get(firstUrl);
    const requestTwo = axios.get(secondUrl);

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          setCourseData(responses[0].data);
          setPartakerData(responses[1].data);
        })
      )
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  let appBar = (
    <AppBar
      position='static'
      style={{
        backgroundColor: 'white',
        color: 'black',
        textDecoration: 'large',
      }}
    >
      <Tabs
        value={value}
        onChange={handleChange}
        aria-label='simple tabs example'
      >
        <TabBigger label='Vista general' {...a11yProps(0)} />
        <TabBigger label='Cursos' {...a11yProps(1)} />
        <TabBigger label='Participantes' {...a11yProps(2)} />
      </Tabs>
    </AppBar>
  );

  let charts = (
    <div>
      <TabPanel value={value} index={0}>
        <CombinedCharts courseData={courseData} partakerData={partakerData} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <CourseCharts courseData={courseData} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <PartakerCharts partakerData={partakerData} />
      </TabPanel>
    </div>
  );

  if (loading) {
    charts = <Spinner />;
  }

  return (
    <div>
      {appBar}
      {charts}
    </div>
  );
};

export default Charts;
