import React from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from 'recharts';

const CourseCharts = (props) => {
  const { partakerData, courseData } = props;
  return (
    <div>
      <div style={{ display: 'flex' }}>
        <div>
          <h3>Total de cursos y participantes por periodo</h3>
          <BarChart
            width={700}
            height={400}
            data={courseData}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey='Cursos' fill='#F0CB6A' minPointSize={5}></Bar>
            <Bar dataKey='Participantes' fill='#8EC3A7' minPointSize={10} />
          </BarChart>
        </div>
      </div>
      <div style={{ display: 'flex' }}>
        <div>
          <h3>Numero de cursos por tipo de curso</h3>
          <BarChart
            width={650}
            height={350}
            data={courseData}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey='Profesional' fill='#DC5457' minPointSize={5}></Bar>
            <Bar dataKey='Docente' fill='#60B7E5' minPointSize={10} />
          </BarChart>
        </div>
        <div>
          <h3>Numero de participantes por tipo de curso</h3>
          <BarChart
            width={650}
            height={350}
            data={partakerData}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey='Profesional' fill='#DC5457' minPointSize={5}></Bar>
            <Bar dataKey='Docente' fill='#60B7E5' minPointSize={10} />
          </BarChart>
        </div>
      </div>
      <div style={{ display: 'flex' }}>
        <div>
          <h3>Numero de cursos por tipo de capacitación</h3>
          <BarChart
            width={650}
            height={350}
            data={courseData}
            margin={{
              top: 20,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey='Curso' stackId='a' fill='#DC5457' />
            <Bar dataKey='Taller' stackId='a' fill='#60B7E5' />
            <Bar dataKey='CursoTaller' stackId='a' fill='#8EC3A7' />
            <Bar dataKey='Conferencia' stackId='a' fill='#F0CB6A' />
            <Bar dataKey='Diplomado' stackId='a' fill='#AB91C5' />
          </BarChart>
        </div>
        <div>
          <h3>Numero de Participantes por tipo de capacitación</h3>
          <BarChart
            width={650}
            height={350}
            data={partakerData}
            margin={{
              top: 20,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey='Curso' stackId='a' fill='#DC5457' />
            <Bar dataKey='Taller' stackId='a' fill='#60B7E5' />
            <Bar dataKey='CursoTaller' stackId='a' fill='#8EC3A7' />
            <Bar dataKey='Conferencia' stackId='a' fill='#F0CB6A' />
            <Bar dataKey='Diplomado' stackId='a' fill='#AB91C5' />
          </BarChart>
        </div>
      </div>
      <div style={{ display: 'flex' }}>
        <div>
          <h3>Numero de cursos por departamento</h3>
          <BarChart
            width={650}
            height={350}
            data={courseData}
            margin={{
              top: 20,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar
              dataKey='ElectricaElectronicaBiomedica'
              stackId='a'
              fill='#DC5457'
            />
            <Bar dataKey='SistemasInformatica' stackId='a' fill='#60B7E5' />
            <Bar
              dataKey='MetalMecanicaAeronautica'
              stackId='a'
              fill='#8EC3A7'
            />
            <Bar dataKey='Industrial' stackId='a' fill='#F0CB6A' />
            <Bar dataKey='CienciasBasicas' stackId='a' fill='#3D5B99' />
            <Bar dataKey='EconomicoAdministrativo' stackId='a' fill='#AB91C5' />
          </BarChart>
        </div>
        <div>
          <h3>Numero de participantes por departamento</h3>
          <BarChart
            width={650}
            height={350}
            data={partakerData}
            margin={{
              top: 20,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray='3 3' />
            <XAxis dataKey='Periodo' />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar
              dataKey='ElectricaElectronicaBiomedica'
              stackId='a'
              fill='#DC5457'
            />
            <Bar dataKey='SistemasInformatica' stackId='a' fill='#60B7E5' />
            <Bar
              dataKey='MetalMecanicaAeronautica'
              stackId='a'
              fill='#8EC3A7'
            />
            <Bar dataKey='Industrial' stackId='a' fill='#F0CB6A' />
            <Bar dataKey='CienciasBasicas' stackId='a' fill='#3D5B99' />
            <Bar dataKey='EconomicoAdministrativo' stackId='a' fill='#AB91C5' />
          </BarChart>
        </div>
      </div>
    </div>
  );
};

export default CourseCharts;
